@import url("https://fonts.googleapis.com/css?family=Inconsolata|Playfair+Display:900i|Sorts+Mill+Goudy");
$static-assets: "//s1.rui.au.reastatic.net/rui-static/";
$static-fonts: $static-assets + "font/";
/* Museo Sans 300 (san serif font) */
@font-face {
  font-family: "Museo-Sans-300";
  src: url($static-fonts + "museo-sans-300-v1.eot");
  src: url($static-fonts + "museo-sans-300-v1.eot?#iefix") format("embedded-opentype"), url($static-fonts + "museo-sans-300-v1.woff") format("woff"), url($static-fonts + "museo-sans-300-v1.ttf") format("truetype"), url($static-fonts + "museo-sans-300-v1.svg#Museo-Sans-300") format("svg");
  font-weight: normal;
  font-style: normal;
}
/* Museo Sans 500 (san serif font) */
@font-face {
  font-family: "Museo-Sans-500";
  src: url($static-fonts + "museo-sans-500-v1.eot");
  src: url($static-fonts + "museo-sans-500-v1.eot?#iefix") format("embedded-opentype"), url($static-fonts + "museo-sans-500-v1.woff") format("woff"), url($static-fonts + "museo-sans-500-v1.ttf") format("truetype"), url($static-fonts + "museo-sans-500-v1.svg#Museo-Sans-500") format("svg");
  font-weight: normal;
  font-style: normal;
}
/* Museo Sans 700 (san serif font) */
@font-face {
  font-family: "Museo-Sans-700";
  src: url($static-fonts + "museo-sans-700-v2.eot");
  src: url($static-fonts + "museo-sans-700-v2.eot?#iefix") format("embedded-opentype"), url($static-fonts + "museo-sans-700-v2.woff") format("woff"), url($static-fonts + "museo-sans-700-v2.ttf") format("truetype"), url($static-fonts + "museo-sans-700-v2.svg#Museo-Sans-700") format("svg");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Museo-Sans-900";
  src: url("../fonts/MuseoSans_900-webfont.eot");
  src: url("../fonts/MuseoSans_900-webfont.eot?#iefix") format("embedded-opentype"), url("../fonts/MuseoSans_900-webfont.woff") format("woff"), url("../fonts/MuseoSans_900-webfont.ttf") format("truetype"), url("../fonts/MuseoSans_900-webfont.svg#font-name") format("svg");
  font-weight: normal;
  font-style: normal;
}
$font-default: "Museo-Sans-300", "Helvetica Neue", Helvetica, Arial, sans-serif;
$font-medium: "Museo-Sans-500", "Helvetica Neue", Helvetica, Arial, sans-serif;
$font-heavy: "Museo-Sans-700", "Helvetica Neue", Helvetica, Arial, sans-serif;
$font-heavier: "Museo-Sans-900", "Helvetica Neue", Helvetica, Arial, sans-serif;
$font-mono: Inconsolata, monospace;
$font-serif-primary: "Sorts Mill Goudy", serif;
$font-serif-secondary: "Playfair Display", serif;



